import React from "react";
import {
  GoogleMap,
  LoadScript,
  StreetViewPanorama,
} from "@react-google-maps/api";

const StreetViewMap = ({center}) => {

  console.log("POSITION CHANGEd",center)

  return (
    <GoogleMap
      center={center}
      zoom={14}
      mapContainerStyle={{
        width: "100%",
        height: "450px",
        backgroundColor: "#eeeeee",
      }}
    >
      <StreetViewPanorama position={center} visible addressControl />
    </GoogleMap>
  );
};

export default StreetViewMap;
