import React, { useEffect, useState } from "react";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";

const Gallery = ({ image_attributes }) => {
  let publicUrl = `${process.env.PUBLIC_URL}`;
  const [Images, setImages] = useState([
    {
      original: publicUrl + "/assets/img/news/19.png",
      thumbnail: publicUrl + "/assets/img/thumbnail.jpg",
      description: "Slides first",
    },
    {
      original: publicUrl + "/assets/img/news/19.png",
      thumbnail: publicUrl + "/assets/img/thumbnail.jpg",
      description: "Slide second",
    },
    {
      original: publicUrl + "/assets/img/news/19.png",
      thumbnail: publicUrl + "/assets/img/thumbnail.jpg",
    },
  ]);

  const galleryImages = () => {
    let images = image_attributes.map((item) => {
      return {
        original: item.location,
        thumbnail: item.location,
        description: "",
      };
    });

    setImages([...images]);
  };

  const [showNav] = useState(true);
  const [isRTL] = useState(true);
  const [thumbnailPosition] = useState("bottom");
  const [showThumbnails] = useState(true);
  const [showIndex] = useState(true);

  useEffect(() => {
    // if (!isEmpty(image_attributes)) {
    galleryImages();
    // }
  }, [image_attributes]);

  return (
    <ImageGallery
      showThumbnails={showThumbnails}
      showIndex={showIndex}
      lazyLoad={true}
      showNav={showNav}
      isRTL={isRTL}
      thumbnailPosition={thumbnailPosition}
      items={Images}
    />
  );
};
export default Gallery;
