import parse from "html-react-parser";
import isNull from "lodash/isNull";

const getEmbedUrl = (url) => {
  let embedUrl = setEmbedUrl({
    url,
  });
  return (
    !isNull(embedUrl) &&
    parse(
      `<iframe id="videoObject" type="text/html" src="${embedUrl}" width="500" height="265" frameborder="0" allowfullscreen></iframe>`
    )
  );
};

const setEmbedUrl = ({ url }) => {
  if (url !== undefined || url !== "") {
    var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=|\?v=)([^#\&\?]*).*/;
    var match = url.match(regExp);
    if (match && match[2].length === 11) {
      // Do anything for being valid
      return `https://www.youtube.com/embed/${match[2]}?autoplay=0&enablejsapi=1`;
    }
  }

  return null;
};

export default getEmbedUrl;
